<template>
  <div class="mylive">
    <div class="mylive-top">
      <van-nav-bar title="我的收藏" left-text="返回" left-arrow @click-left="goback" />
    </div>
    <div class="mylivebox">
      <div class="bodybox" v-if="liveData.length>0">
        <div class="itembox" v-for="(item,index) in liveData" :key="index">
          <div class="itembox-body">
            <div class="elemtdata flexbetween">
              <div class="imgbox">
                <img :src="item.smallImg" alt />
              </div>
              <div class="TxtandPr flexbetween">
                <div class="Txtbox">
                  <strong>{{item.name}}</strong>
                  <p>{{item.enname}}</p>
                </div>
                <div class="pricebox">
                  <strong>￥{{item.price}}</strong>
                </div>
              </div>
            </div>
          </div>
          <div class="itembox-foot flexbetween">
            <div class="detailsbox flexbetween" @click="godetails(item.pid)">
              <span>查看详情</span>
              <van-icon name="arrow" size="14" color="#3a63f1" />
            </div>
            <div class="delbtn" @click="dellive(item.pid,index)">删除</div>
          </div>
        </div>
      </div>
      <!-- 空显示 -->
      <div v-else class="konbox">
        <van-empty description="空空如也">
          <van-button
            round
            type="danger"
            class="bottom-button"
            color="#3a63f1"
            @click="gohome"
          >去加入收藏吧!</van-button>
        </van-empty>
      </div>
    </div>
  </div>
</template>

<script>
import "../assets/less/mylive.less";
import { utils } from "../assets/js/utils";
export default {
  data() {
    return {
      liveData: [],
    };
  },
  created() {
    this.getliveData();
  },
  methods: {
    //返回
    goback() {
      this.$router.go(-1);
    },
    //获取收藏数据
    getliveData() {
      let tokenString = localStorage.getItem("CSTK");
      if (!tokenString) {
        return this.$router.push("/login");
      }
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      this.axios({
        method: "GET",
        url: "/findAllLike",
        params: {
          appkey: this.appkey,
          tokenString,
        },
      })
        .then((result) => {
          this.$toast.clear();

          if (result.data.code == 2000) {
            this.liveData = result.data.result;
          }
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },
    //删除收藏商品
    dellive(pid, index) {
      let tokenString = localStorage.getItem("CSTK");
      if (!tokenString) {
        return this.$router.push("/login");
      }
      let data = utils.queryString({
        appkey: this.appkey,
        pid,
        tokenString,
      });
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      this.axios({
        method: "POST",
        url: "/notlike",
        data,
      })
        .then((result) => {
          this.$toast.clear();
          if (result.data.code == 900) {
            this.liveData.splice(index, 1);
          }
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },
    //查看详情
    godetails(pid) {
      this.$router.push({ name: "Detail", query: { pid } });
    },
    //跳转home界面
    gohome() {
      this.$router.push("/main");
    },
  },
};
</script>

<style lang="less" scoped>
</style>